import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormControl, FormGroupDirective, NgForm } from '@angular/forms';

import { UsersService } from './../users/users.service';
import { ErrorStateMatcher } from '@angular/material';

export class MyErrorStateMatcher implements ErrorStateMatcher {
  isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
    const invalidCtrl = !!(control && control.invalid && control.parent.dirty);
    const invalidParent = !!(control && control.parent && control.parent.invalid && control.parent.dirty);

    return (invalidCtrl || invalidParent);
  }
}

@Component({
  selector: 'app-registration',
  templateUrl: './registration.component.html',
  styleUrls: ['./registration.component.css']
})
export class RegistrationComponent implements OnInit {

  formUser: FormGroup; //represent the form
  matcher = new MyErrorStateMatcher();
  constructor(
    private formBuilder: FormBuilder,
    private usersService: UsersService
  ) { }

  ngOnInit() {
    this.formUser = this.formBuilder.group({
      first_name: [null, Validators.required],
      last_name: [null, Validators.required],
      role: [null, Validators.required],
      email: [null, Validators.email],
      password: ['', [Validators.required]],
      confirmPassword: ['']
    }, { validator: this.checkPasswords });
  }
  checkPasswords(group: FormGroup) { // here we have the 'passwords' group
    let pass = group.controls.password.value;
    let confirmPass = group.controls.confirmPassword.value;
    return pass === confirmPass ? false : { notSame: true }
  }

  setForm(data) {
    this.formUser.patchValue({
        password: data.password,
        first_name: data.first_name,
        last_name: data.last_name,
        email: data.email,
        role: data.role
    })
  }

  onSubmit() {
      this.postUser();    
  }
  
  postUser() {
    this.usersService.postRegistrationUser(this.formUser.value).subscribe(
      (data:any) => {
        this.formUser.reset()
      },
      error => {console.log("Erro ao salvar user: ", error.error), alert(error.error)}
    )
  }
}
