import { RulesService } from './rules.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-rules',
  templateUrl: './rules.component.html',
  styleUrls: ['./rules.component.css']
})
export class RulesComponent implements OnInit {

  constructor(private rulesService: RulesService) { }
  private inconsistencies: any[];

  ngOnInit() {
    console.log("Get inconsistencies")
    this.rulesService.getAllInconsistencies().subscribe(
      (data: any) => this.inconsistencies = data,
      error => {console.log("Erro ao salvar user: ", error.error), alert(error.error)}
    );
  }
}
